import React, { useEffect } from 'react'
import { observer } from 'mobx-react'
import { useStores } from '../stores'
import InfiniteScroll from 'react-infinite-scroll-component'
import { Divider, List, Skeleton, Button, message } from 'antd'
import styled from 'styled-components'


const Img = styled.img`
  width: 200px;
  height: 100px;
  object-fit: contain;
  border: 1px solid #ccc;
`
const StyledButton = styled(Button)`
  margin-left: 10px;
`

const CopyButton = styled.button`
  margin-left: 10px;
  padding: 0;

  color: grey;
  border: none;
  border-radius: 4px;
  cursor: pointer;
`
const StyledList = styled.div`
  white-space: nowrap;
  border: 1px soild red;
  overflow: auto;
`

const Component = observer(() => {
  const { HistoryStore } = useStores()

  const loadMoreData = () => {
    if (HistoryStore.isLoading) {
      return
    }
    HistoryStore.find()
    console.log('åŠ è½½æ›´å¤šä¸­..')
    console.log(HistoryStore.list)
  }

  useEffect(() => {
    HistoryStore.find()
    console.log('åŠ è½½ç»„ä»¶')
    console.log(HistoryStore.list)

    return () => {
      console.log('å¸è½½ç»„ä»¶')
      HistoryStore.reset()
      // React StrictMode renders components twice on dev server
      // https://stackoverflow.com/questions/60618844/react-hooks-useeffect-is-called-twice-even-if-an-empty-array-is-used-as-an-ar
    }
  }, [])

  const bindOnClick1 = (url) => {
    navigator.clipboard.writeText(`${url}`)
    message.success('å¤åˆ¶æˆåŠŸ')
  }

  return (
    <StyledList
      id="scrollableDiv"
      style={{
        height: 800,
        overflow: 'auto',
        padding: '0 16px',
        border: '1px solid rgba(140, 140, 140, 0.35)',
      }}
    >
      <InfiniteScroll
        dataLength={HistoryStore.list.length}
        next={loadMoreData}
        hasMore={HistoryStore.hasMore}
        loader={
          <Skeleton
            avatar
            paragraph={{
              rows: 1,
            }}
            active
          />
        }
        endMessage={<Divider plain>It is all, nothing more ğŸ¤</Divider>}
        scrollableTarget="scrollableDiv"
      >
        <List dataSource={HistoryStore.list}
          renderItem={
            item => (
              <List.Item key={item.cid} style={{ justifyContent: 'left' }}>
                <div>
                  <Img src={item.attributes.url.attributes.url} style={{ height: '100px' }} />
                </div>
                <div style={{ marginLeft: '30px' }}>
                  <h4>{item.attributes.filename}</h4>
                </div>
                <div style={{ marginLeft: '30px' }}>
                  <a target="_blank" href={item.attributes.url.attributes.url}>{item.attributes.url.attributes.url}</a>
                </div>
                <CopyButton onClick={() => bindOnClick1(item.attributes.url.attributes.url)}> ç‚¹å‡»å¤åˆ¶ </CopyButton>
                <StyledButton onClick = { () => { HistoryStore.destory(item.id) } }> Delete </StyledButton>
              </List.Item>
            )
          }
        />
      </InfiniteScroll>
    </StyledList>
  );
})

export default Component